*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, body, ul, p {
  margin: 0;
}

ul, ol {
  padding: 0;
  list-style: none;
}

button {
  color: currentColor;
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  line-height: inherit;
  word-spacing: inherit;
  cursor: pointer;
  border: none;
}

img {
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

input {
  border: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

canvas {
  background-color: #0000;
  background-image: linear-gradient(90deg, #000000b3, #000000b3), url("https://starwalk.space/gallery/images/what-is-space/1920x1080.jpg");
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  width: 100%;
  height: 100%;
}

.modal {
  --size: 30px;
  opacity: 0;
  pointer-events: none;
  backdrop-filter: blur(3px);
  background-color: #00000080;
  border-radius: 5px;
  width: 100%;
  max-width: 115px;
  padding: 5px;
  transition: opacity .3s, left .3s, top .3s, transform .3s;
  position: absolute;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.flex {
  --flex-items: 3;
  --flex-items-gap: 5px;
  gap: var(--flex-items-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex > li {
  flex-basis: calc((100% - (var(--flex-items)  - 1) * var(--flex-items-gap)) / var(--flex-items));
}

.modal button {
  height: var(--size);
  width: var(--size);
  background-color: #eee;
  border-radius: 5px;
  transition: background-color .3s;
}

.modal button:hover, .modal button:focus {
  background-color: #ccc;
}

.modal button.clear {
  margin: 10px auto 0;
  display: block;
}
